import { useQueries } from "@tanstack/react-query"
import { getContentSourceByID } from "../services/getContentSourceByID"
import { contentSourcesByIDQueryKeys } from "../utils/contentSourcesByIDQueryKeys"

function useContentSourcesByID(ids: Array<string>) {
  return useQueries({
    queries: ids.map(id => ({
      queryKey: contentSourcesByIDQueryKeys.sourceById(id),
      queryFn: () => getContentSourceByID({ id }),
    })),
  })
}

export { useContentSourcesByID }
